.page {
  margin: 0;
  padding: 0;
  background-color: #e5e5e5;
}
@media (min-width: 1200px) {
  .page {
    min-width: 1200px;
  }
}

.page__body {
  max-width: 1200px;
  height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  margin: 0 auto;
  padding: 0;
}
@media (min-width: 768px) {
  .page__body {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .page__body {
    padding: 0;
    margin: 0 auto;
  }
}

.main-header {
  display: flex;
  justify-content: space-between;
}

.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  max-width: 100%;
  transform: translateX(-50%);
  z-index: 1;
}
@media (min-width: 768px) {
  .main-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    max-width: 100%;
    transform: translateX(-50%);
  }
}

.main-navigation {
  width: 100%;
  padding: 0;
  margin: 0;
}
.main-navigation--opened {
  z-index: 1;
}
@media (min-width: 768px) {
  .main-navigation {
    z-index: 0;
  }
}

.main-navigation__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  width: 23px;
  height: 22px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-navigation__toggle:hover {
  opacity: 70%;
}
.main-navigation__toggle:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__wrapper {
  padding-top: 56px;
}
@media (min-width: 768px) {
  .main-navigation__wrapper {
    padding: 0;
  }
}

.main-navigation--opened .main-navigation__wrapper {
  padding: 0;
}

.main-navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-navigation__list {
    flex-direction: row;
    background-color: #ffffff;
  }
}

.main-navigation__list li {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  width: 100%;
  text-align: center;
}
.main-navigation__list li:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .main-navigation__list li {
    background-color: #ffffff;
  }
  .main-navigation__list li:hover {
    background-color: #ffffff;
  }
}

.main-navigation__list-item {
  padding: 0;
  margin: 0;
  min-height: 60px;
  border-bottom: 1px #e5e5e5;
}
@media (min-width: 768px) {
  .main-navigation__list-item {
    background-color: #ffffff;
  }
  .main-navigation__list-item--active {
    color: #766357;
  }
}

.main-navigation__link {
  display: block;
  padding: 15px 25px;
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
}
.main-navigation__link:active {
  opacity: 70%;
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .main-navigation__link {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    background-color: #ffffff;
  }
  .main-navigation__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .main-navigation__link:active {
    color: #000000;
    opacity: 0.3;
  }
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list-item {
    display: none;
  }
}
.main-navigation--closed .main-navigation__list-item .main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation--closed .main-navigation__toggle {
  position: absolute;
  background-image: url("../img/icon-menu.svg");
  width: 23px;
  height: 22px;
  background-color: #ffffff;
  top: 17px;
  right: 20px;
  border: none;
  cursor: pointer;
}
.main-navigation--closed .main-navigation__toggle:hover {
  opacity: 70%;
}
.main-navigation--closed .main-navigation__toggle:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .main-navigation--closed .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--opened .main-navigation__toggle {
  position: absolute;
  background-image: url("../img/icon-cross.svg");
  width: 23px;
  height: 22px;
  background-color: #81b3d2;
  top: 17px;
  right: 20px;
  border: none;
  cursor: pointer;
}
.main-navigation--opened .main-navigation__toggle:hover {
  opacity: 70%;
}
.main-navigation--opened .main-navigation__toggle:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .main-navigation--opened .main-navigation__toggle {
    display: none;
  }
}

