}

.advantages__block-description {
  text-align: center;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.advantages__list {
  display: grid;
  margin: 0;
  text-align: center;
  list-style-type: none;
  padding: 0;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__list-color-whisper {
  background-color: #eeeeee;
}

.advantages__list-item {
  background-color: #ffffff;
  position: relative;
}
.advantages__list-item--small-square {
  padding: 26px 20px 24px 20px;
  background-color: #eeeeee;
}
.advantages__list-item--small-square::after {
  content: "";
  display: block;
  position: absolute;
  left: 18px;
  bottom: 0;
  width: 284px;
  height: 1px;
  background-color: #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__list-item--small-square::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__list-item--small-square {
    padding: 42px 36px 63px 35px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-item--small-square {
    padding: 43px 55px 61px;
  }
}
.advantages__list-item--beautiful-road {
  padding: 24px 41px 26px;
  background-color: #eeeeee;
}
.advantages__list-item--beautiful-road::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 18px;
  width: 284px;
  height: 1px;
  background-color: #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__list-item--beautiful-road::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__list-item--beautiful-road {
    padding: 43px 54px 41px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-item--beautiful-road {
    padding: 42px 55px 62px;
  }
}
.advantages__list-item--few-tourists {
  padding: 25px 31px 26px;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__list-item--few-tourists {
    padding: 42px 60px 42px 61px;
  }
}
@media (min-width: 1200px) {
  .advantages__list-item--few-tourists {
    padding: 44px 55px 60px;
  }
}

.advantages__item-title {
